<script>
export default {
  name: "error-page",
  components: {},
  render() {
    // <van-button round type='info'>
    //       重新定位
    //     </van-button>
    return (
      <div class="container">
        <div class="error">
          <h1>404错误</h1>
          <p class="text">哎呀 , 找不到了</p>
          <section class="container-container">
            <span>4</span>
            <span>
              <span class="screen-reader-text"></span>
            </span>
            <span>4</span>
          </section>
        </div>
      </div>
    );
  },
  data() {
    self = this;
    return {
      errorImg: require("../assets/img/error.jpg"),
      errorInfo: "",
    };
  },
  props: {},
  // 下面的一些方法里面,不应该使用箭头函数来定义 watcher 函数
  // 理由是箭头函数绑定了父级作用域的上下文
  // 但是已经定义了一个全局self,所以也就没有关系了
  watch: {},
  computed: {
    //经过处理需要渲染到页面上的值,
    //可以将接口的JSON放到这
    //从而省略在方法区处理数据
    //也不会修改元数据
  },
  beforeRouteEnter(to, from, next) {
    const {
      query: { type },
    } = to;
    next((vm) => {
      switch (type) {
        case "0":
          vm.errorInfo = "GUID格式可能错误,请检查!";
          break;
        case "1":
          vm.errorInfo = "检查链接是否缺少必要内容!";
          break;
        default:
          vm.errorInfo = "未知错误,请重新从短信打开链接重试!";
          break;
      }
      vm.$notify(vm.errorInfo, 3000);
    });
  },
  methods: {},
  created() {},
  mounted() {},
};
</script>
<style scoped>

.container {
  width: 100%;
  height: 100vh;
  background-color: #416475;
  display: flex;
  align-items: center;
  justify-content: center;
}
.error {
  text-align: center;
  user-select: none;
}
h1 {
  color: #a8d5eb;
  letter-spacing: 6px;
}

.text {
  font-size: 28px;
  color: #93b3c4;
  font-weight: 600;
  margin-top: 20px;
}

.container-container {
  position: relative;
  font-family: Arial, sans-serif;
  font-size: 240px;
  font-weight: 800;
}
.container-container > span {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}

.container-container > span:nth-child(1) {
  color: #d1f2a5;
  animation: colordancing 3s infinite;
}
.container-container > span:nth-child(3) {
  color: #f56991;
  animation: colordancing2 3s infinite;
}
.container-container > span:nth-child(2) {
  width: 140px;
  height: 140px;
}
.container-container > span:nth-child(2):before,
.container-container > span:nth-child(2):after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: shadowdancing 3s infinite;
}
.container-container > span:nth-child(2):before {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  transform: rotate(45deg);
}

.screen-reader-text {
  position: absolute;
  top: -9999em;
  left: -9999em;
}

@media screen and (max-width: 600px) {
  .container-container {
    font-size: 120px;
    top: 10px;
    transition: all 1s;
  }
  .container-container > span:nth-child(2) {
    width: 72px;
    height: 72px;
  }
  .container-container > span:nth-child(2):before,
  .container-container > span:nth-child(2):after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: inherit;
    height: inherit;
    border-radius: 50%;
    animation: shadowdancing2 3s infinite;
  }
}

@keyframes shadowdancing {
  0% {
    box-shadow: inset 30px 0 0 rgba(209, 242, 165, 0.4),
      inset 0 30px 0 rgba(239, 250, 180, 0.4),
      inset -30px 0 0 rgba(255, 196, 140, 0.4),
      inset 0 -30px 0 rgba(245, 105, 145, 0.4);
  }
  25% {
    box-shadow: inset 30px 0 0 rgba(245, 105, 145, 0.4),
      inset 0 30px 0 rgba(209, 242, 165, 0.4),
      inset -30px 0 0 rgba(239, 250, 180, 0.4),
      inset 0 -30px 0 rgba(255, 196, 140, 0.4);
  }
  50% {
    box-shadow: inset 30px 0 0 rgba(255, 196, 140, 0.4),
      inset 0 30px 0 rgba(245, 105, 145, 0.4),
      inset -30px 0 0 rgba(209, 242, 165, 0.4),
      inset 0 -30px 0 rgba(239, 250, 180, 0.4);
  }
  75% {
    box-shadow: inset 30px 0 0 rgba(239, 250, 180, 0.4),
      inset 0 30px 0 rgba(255, 196, 140, 0.4),
      inset -30px 0 0 rgba(245, 105, 145, 0.4),
      inset 0 -30px 0 rgba(209, 242, 165, 0.4);
  }
  100% {
    box-shadow: inset 30px 0 0 rgba(209, 242, 165, 0.4),
      inset 0 30px 0 rgba(239, 250, 180, 0.4),
      inset -30px 0 0 rgba(255, 196, 140, 0.4),
      inset 0 -30px 0 rgba(245, 105, 145, 0.4);
  }
}

@keyframes shadowdancing2 {
  0% {
    box-shadow: inset 20px 0 0 rgba(209, 242, 165, 0.4),
      inset 0 20px 0 rgba(239, 250, 180, 0.4),
      inset -20px 0 0 rgba(255, 196, 140, 0.4),
      inset 0 -20px 0 rgba(245, 105, 145, 0.4);
  }
  25% {
    box-shadow: inset 20px 0 0 rgba(245, 105, 145, 0.4),
      inset 0 20px 0 rgba(209, 242, 165, 0.4),
      inset -20px 0 0 rgba(239, 250, 180, 0.4),
      inset 0 -20px 0 rgba(255, 196, 140, 0.4);
  }
  50% {
    box-shadow: inset 20px 0 0 rgba(255, 196, 140, 0.4),
      inset 0 20px 0 rgba(245, 105, 145, 0.4),
      inset -20px 0 0 rgba(209, 242, 165, 0.4),
      inset 0 -20px 0 rgba(239, 250, 180, 0.4);
  }
  75% {
    box-shadow: inset 20px 0 0 rgba(239, 250, 180, 0.4),
      inset 0 20px 0 rgba(255, 196, 140, 0.4),
      inset -20px 0 0 rgba(245, 105, 145, 0.4),
      inset 0 -20px 0 rgba(209, 242, 165, 0.4);
  }
  100% {
    box-shadow: inset 20px 0 0 rgba(209, 242, 165, 0.4),
      inset 0 20px 0 rgba(239, 250, 180, 0.4),
      inset -20px 0 0 rgba(255, 196, 140, 0.4),
      inset 0 -20px 0 rgba(245, 105, 145, 0.4);
  }
}
@keyframes colordancing {
  0% {
    color: #d1f2a5;
  }
  25% {
    color: #f56991;
  }
  50% {
    color: #ffc48c;
  }
  75% {
    color: #effab4;
  }
  100% {
    color: #d1f2a5;
  }
}
@keyframes colordancing2 {
  0% {
    color: #ffc48c;
  }
  25% {
    color: #effab4;
  }
  50% {
    color: #d1f2a5;
  }
  75% {
    color: #f56991;
  }
  100% {
    color: #ffc48c;
  }
}
</style>
